.portfolio_part {
  margin-bottom: 4rem;
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

.images {
  display: flex;
  width: calc(100% - 2rem);
  overflow-x: scroll;
  scroll-behavior: smooth;
  scrollbar-width: none;
  padding: 0 1rem;
  gap: 1rem;
}

.images::-webkit-scrollbar {
  display: none;
}

.images img,
.images video,
.images iframe {
  height: 70vh;
  flex-shrink: 0;
}

.images iframe {
  width: 70vh;
}

.text {
  font-size: 1.2rem;
  padding: 0 1rem;
}

.move {
  display: flex;
  /* justify-content: space-between; */
  width: calc(100% - 2rem);
  padding: 0 1rem;
}

.moveRight,
.moveLeft {
  font-size: 2rem;
}

.move img {
  height: 2em;
}

.video {
  display: flex;
  justify-content: center;
}
